export default {
	name: 'chat-input',
	data () {
		return {
			val: ''
		}
	},

	props: ['focus', 'inputValue', 'isShow'],

	methods: {
		sendMessge () {
			if (this.val.trim() == '') {
				this.val = ''
			} else {
				this.$emit('send-message', {
					type: 'text',
					content: this.val
				})

				this.val = ''
				uni.hideKeyboard()
			}
		},

		// 切换表情页面
		onTap () {
			this.$emit('show-emoji')
			this.$emit('updata-val', this.val)
		},

		uploadImg () {
			this.$emit('on-upload')
		}
	},

	watch: {
		isShow (val) {
			this.val = this.inputValue
		}
	},
}
